import { Component, Input, OnInit } from '@angular/core';
import { Animation, AnimationController } from '@ionic/angular';

import {
  Board,
  DGAudio
} from 'dice-guardians-module'

@Component({
  selector: 'app-splash',
  templateUrl: './splash.component.html',
  styleUrls: ['./splash.component.scss'],
})
export class SplashComponent implements OnInit {
  @Input() board:Board

  text:string = 'Loading, please wait...'
  ready:boolean = false
  animationCtrl: AnimationController
  config:any = {
    volume: {
      overall:1,
      music:0.6,
      effect:0.8
    }
  }

  constructor(
    animationCtrl: AnimationController
  ) {
    this.animationCtrl = animationCtrl
  }

  ngOnInit() {
    let self = this
    this.board.events.on('ready', () => {
      setTimeout(() => {
        self.text = 'Game ready'
        self.ready = true
      }, 1000)
    })

    let config:any = localStorage.getItem('audio-config')

    if (!config){
      config = this.config
      localStorage.setItem('audio-config', JSON.stringify(config))
      return
    } else {
      config = JSON.parse(config)
    }

    this.config = config
  }

  async hideSplash(){
    //this.board.splashDisplay = 'none'
    const splash = document.querySelector('.overall')
    console.log('splash', splash)
    const animation = this.animationCtrl.create()
    .addElement(splash)
    .duration(350)
    .easing('ease-in')
    .fromTo('transform', 'translateY(0px)', 'translateY(100vh)')
    .fromTo('opacity', '1', '0')
    await animation.play()
    console.log(animation)
    splash.parentNode.removeChild(splash)

    this.board.events.emit('splashHidden', {})

    const audio = new DGAudio('assets/audio/music-zapsplat-beach-cruzin.mp3', 'music')
    audio.play()
  }

  updateAudioSettings(){
    let dom:any = document.querySelector('ion-toggle[value="music"]')
    const music:any = dom.checked
    dom = document.querySelector('ion-toggle[value="effect"]')
    const fx:any = dom.checked

    let config:any = localStorage.getItem('audio-config')

    if (!config){
      config = {
        volume: {
          overall:1,
          music:0.6,
          effect:0.8
        }
      }

      localStorage.setItem('audio-config', JSON.stringify(config))
    } else {
      config = JSON.parse(config)
    }

    if (music){
      config.volume.music = 0.6
    } else {
      config.volume.music = 0
    }

    if (fx){
      config.volume.effect = 0.6
    } else {
      config.volume.effect = 0
    }

    this.config = config
    localStorage.setItem('audio-config', JSON.stringify(config))

  }
}
