import { Input, Component, OnInit } from '@angular/core';
import {
  Card,
  Board,
  Player,
  Dice,

  DGAudio
} from 'dice-guardians-module'

@Component({
  selector: 'app-card',
  templateUrl: './card.component.html',
  styleUrls: ['./card.component.scss'],
})
export class CardComponent implements OnInit {
  @Input() public board:Board
  @Input() public card: Card
  @Input() public player:Player
  @Input() public width: number

  @Input() public innerAlign:string = "space-evenly"
  @Input() public disableMenu:string = "flex"

  dice:Dice = new Dice(0)

  @Input() public showMagnify: boolean = false

  statIcons: Array<string> = [
    'assets/icons/mov-w-t.png',
    'assets/icons/btl-w-t.png',
    'assets/icons/vit.png',
    'assets/icons/cst-w-t.png',
  ]

  statToIcons:any = {
    'hex' : 'assets/icons/hex.png'
  }

  constructor(
  ){

  }

  ngOnInit(){
  }

  toggleAbilityTooltip(ability:any){
    for (let i = 0; i < this.card.abilities.length; i++){
      if (this.card.abilities[i].id === ability.id){
        if (!this.card.showAbility){
          //this.card.abilities[i]['hidden'] = 'visible'

          this.card.showAbility = true
          this.card.activeAbility = this.card.abilities[i]
        } else {
          //this.card.abilities[i]['hidden'] = 'hidden'

          this.card.showAbility = false
          this.card.activeAbility = this.card.abilities[i]
        }
      }
    }

    console.log(this.card)
  }

  magnify(card:Card){
    const sound = new DGAudio('assets/audio/card-view.mp3')
    sound.play()
    this.board.cardModal(card)
  }

  pad(num:number, size:number=2): string {
    let s = num+"";
    while (s.length < size) s = "0" + s;
    return s;

  }
}
