import { Component, Input, OnInit } from '@angular/core';
import {
  Board
} from 'dice-guardians-module'
@Component({
  selector: 'app-game-monitor',
  templateUrl: './game-monitor.component.html',
  styleUrls: ['./game-monitor.component.scss'],
})
export class GameMonitorComponent implements OnInit {
  @Input() board:Board

  monitorTitle:string=''
  monitorText:string=''

  constructor() { }

  ngOnInit() {
    this.monitorTitle = this.board.monitorTitle
    this.monitorText = this.board.monitorText
  }

}
