import { Component, Input, OnInit } from '@angular/core';

import {
  Player
} from 'dice-guardians-module'

@Component({
  selector: 'app-energy-pool',
  templateUrl: './energy-pool.component.html',
  styleUrls: ['./energy-pool.component.scss'],
})
export class EnergyPoolComponent implements OnInit {
  @Input() player: Player

  tooltipClasses:any = {}

  visible:Array<any> = []
  constructor() {}

  ngOnInit() {
    const self = this

    this.player.energyPool.events.on('mint', () => {
      self.getVisibleEnergies(self)
    })
    this.player.energyPool.events.on('burn', () => {
      self.getVisibleEnergies(self)
    })
    this.player.energyPool.events.on('reset', () => {
      self.getVisibleEnergies(self)
    })

    self.getVisibleEnergies(self)
  }

  async sleep(ms:number = 500){
    return new Promise((resolve, reject) => {
      try {
        setTimeout(() =>{resolve()})
      } catch (e) {reject(e)}
    })
  }
  async getVisibleEnergies(ctx:any){
    await this.sleep()
    console.log(this.player.energyPool)
    let visible = []
    const iterator = ctx.player.energyPool.types.values()

    for (let i = 0; i < ctx.player.energyPool.types.size; i++){
      const type = iterator.next().value
      if (!ctx.tooltipClasses[type]){ctx.tooltipClasses[type] = 'hidden'}
      const value = ctx.player.energyPool.get(type)

      if (value > 0){
        visible.push({type, value})
      }
    }
    console.log(visible)
    ctx.visible = visible
  }


  toggleTooltip(energy:string){
    console.log('pre', this.tooltipClasses)
    if (this.tooltipClasses[energy] === 'visible'){
      this.tooltipClasses[energy] = 'hidden'
    } else {
      const keys = Object.keys(this.tooltipClasses)

      for (let i = 0; i < keys.length; i++){
        this.tooltipClasses[keys[i]] = 'hidden'
      }
      this.tooltipClasses[energy] = 'visible'
    }
    console.log('post', this.tooltipClasses)

  }

  getToolTipClass(energy:string){
    return this.tooltipClasses[energy]
  }

}
